import React, { Component } from 'react';
import { Row, Col, Form, Select, Input, Checkbox, Button, Radio } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { BtnSearch, BtnClear } from 'components/common/button';

const { Option } = Select;

@Form.create({ name: 'formScholarshipApproval' })
class SearchBar extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  toggle = () => {
    const { expand } = this.state;
    this.setState({ expand: !expand });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="ant-advanced-search-form" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} classes={{ isShow: false }} />
          <Col span={8}>
            <Form.Item label="Hc k">
              {getFieldDecorator('system5', {
                rules: [
                  {
                    required: true,
                    message: 'Input something!',
                  },
                ],
              })(
                <Select>
                  <Option value="1">Cp trng</Option>
                  <Option value="2">Cp tnh</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Nm hc">
              {getFieldDecorator('system6', {
                rules: [
                  {
                    required: true,
                    message: 'Input something!',
                  },
                ],
              })(
                <Select>
                  <Option value="1">2017-2018</Option>
                  <Option value="2">2018-2019</Option>
                  <Option value="3">2019-2020</Option>
                  <Option value="4">2020-2021</Option>
                </Select>
              )}
            </Form.Item>
          </Col>

          <Col span={8}>
            <Form.Item label="Xut sc">{getFieldDecorator('system11', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Gii">{getFieldDecorator('system12', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Kh">{getFieldDecorator('system13', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Phng n xt">
              {getFieldDecorator('system7', {
                rules: [
                  {
                    required: true,
                    message: 'Input something!',
                  },
                ],
              })(
                <Select>
                  <Option value="1">Cp trng</Option>
                  <Option value="2">Cp tnh</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Xt theo s xut">
              {getFieldDecorator('system8', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Xt theo % sinh vin c hc bng">
              {getFieldDecorator('system9', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Xt theo s tin">
              {getFieldDecorator('system10', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item>
              {getFieldDecorator('system14', {})(<Checkbox>Khng tnh im mn hc li </Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item>
              {getFieldDecorator('system15', {})(<Checkbox>Ly hn </Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item>
              {getFieldDecorator('system16', {})(<Checkbox>Ly km </Checkbox>)}
            </Form.Item>
          </Col>
          {/* <Col span={6}>
            <Form.Item>
              {getFieldDecorator('system19', {})(<Checkbox> Chn tt c </Checkbox>)}
            </Form.Item>
          </Col> */}
          <Col span={12}>
            <Form.Item>
              {getFieldDecorator('system17', {})(<Radio>Cha nhn c hc bng</Radio>)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item>
              {getFieldDecorator('system18', {})(<Radio> nhn c hc bng</Radio>)}
            </Form.Item>
          </Col>

          <Col span={8}>S tin  xt : 10</Col>
          <Col span={8}>S sv c hc bng : 10</Col>
          <Col span={8}>% sv c hc bng : 8</Col>
          <Col span={8}>S sinh vin : 8</Col>
          <Col span={8}>S tin  xt : 8</Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <Button style={{ height: 38 }} type="primary">
                {' '}
                Xt hc bng
              </Button>
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



